import React, { Component } from 'react';
import './index.css'
import storage from '../models/Storage';
import { Input,Icon} from 'antd';
import { Checkbox } from 'antd';
import { List} from 'antd';

class index extends Component {
    constructor(props){
        super(props);
        this.state={
            value:'',
            Doing:[],
            Done:[]
        }
    }
    componentDidMount(){
        let todolist = storage.get("Todolist");
        let sublist = storage.get("Sublist");
        if(todolist){
            this.setState({
                Doing:todolist    
            })
        };
        if(sublist){
            this.setState({
                Done:sublist
            })
        };
    }
    getValue(e){
        this.setState({
            value:e.target.value
        })
    }
    handelAdd=()=>{
        if( this.state.value!==''){
            this.state.Doing.push({name:this.state.value,isCheck:false})
            let arr =this.state.Doing
            this.setState({
                Doing:arr,
                value:''
            })
            storage.set("Todolist",arr)
        }
    }
    handelGoDone(index){
        this.state.Done.push({name:this.state.Doing[index].name,isCheck:true});
        let arrTow = this.state.Done;
        this.state.Doing.splice(index,1);
        let arr = this.state.Doing;
        this.setState({
            Doing:arr,
            Done:arrTow
        })
        storage.set("Todolist",arr);
        storage.set("Sublist",arrTow);
    }
    DeleDoing(index){
        this.state.Doing.splice(index,1);
        let arr = this.state.Doing;
        this.setState({
            Doing:arr
        })
        storage.set("Todolist",arr);
    }
    handelGoDoing(index){
        this.state.Doing.push({name:this.state.Done[index].name,isCheck:false});
        let arr = this.state.Doing;
        this.state.Done.splice(index,1);
        let arrTow = this.state.Done;
        this.setState({
            Doing:arr,
            Done:arrTow
        })
        storage.set("Todolist",arr);
        storage.set("Sublist",arrTow);
    }
    DeleDone(index){
        this.state.Done.splice(index,1);
        let arr = this.state.Done;
        this.setState({
            Done:arr
        })
        storage.set("Sublist",arr);
    }
    render() {
        return (
            <div className='container'>
                <Input placeholder="请输入Todo" onChange={this.getValue.bind(this)} onPressEnter={this.handelAdd} />
                <div>
                    <h2>正在进行 {this.state.Doing.length} </h2>
                    <List
                        itemLayout="horizontal"
                        dataSource={this.state.Doing}
                        renderItem={(item,index) => (
                        <List.Item 
                        actions={[
                                <Icon style={{color:'#008DFF',fontSize:'22px'}} onClick={this.handelGoDone.bind(this,index)} type="plus-circle" />
                            ,
                                <Icon style={{color:'#FF4D4F',fontSize:'22px'}} onClick={this.DeleDoing.bind(this,index)} type="close-circle" />
                            ]}
                        >
                            <List.Item.Meta
                                avatar={<Checkbox checked={item.isCheck} />}
                                description={item.name}
                            />
                        </List.Item>
                        )}
                    />
                </div>
                <div>
                    <h2>已完成 {this.state.Done.length} </h2>
                    <List
                        itemLayout="horizontal"
                        dataSource={this.state.Done}
                        renderItem={(val,index) => (
                        <List.Item 
                        actions={[
                            <Icon style={{color:'#008DFF',fontSize:'22px'}} onClick={this.handelGoDoing.bind(this,index)} type="vertical-align-top" />
                            ,
                            <Icon style={{color:'#FF4D4F',fontSize:'22px'}} onClick={this.DeleDone.bind(this,index)} type="close-circle" />
                        ]}
                        >
                            <List.Item.Meta
                                avatar={<Checkbox checked={val.isCheck} />}
                                description={val.name}
                            />
                        </List.Item>
                        )}
                    />
                </div>
            </div>
        );
    }
}

export default index;